@import url(https://fonts.googleapis.com/css2?family=Baskervville&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: "Baskervville", "Playfair Display", Georgia, "Times New Roman",
    serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.readingText {
  width: 72%;
  margin: auto;
  text-align: justify;
}

.newTextButton {
  margin-bottom: 10px;
  width: 100px;
}

.inner {
  margin: auto;
}

.navbar1 {
  background-color: rebeccapurple !important;
  color: white;
}

.navitem {
  color: black;
}

.jumbo {
  background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.jumbo-text {
  width: 70%;
  margin: auto;
}

.subcontainer {
  background: white;
}

.enterNewNote {
  margin-bottom: 10px;
}

.show-hide {
  margin-bottom: 10px;
}

.keyphrase {
  margin-left: 5px;
  margin-right: 5px;
  height: 40px;
}

.selector {
  margin-top: 10px;
}

div.dropdown {
  display: inline;
  margin-right: 5px;
}

.newnote {
  margin-bottom: 10px;
}

.tags {
  margin-left: 5px;
  margin-right: 5px;
}

div.tags.dropdown {
  display: inline;
}

div.options.dropdown {
  display: inline;
}

.createNewTag {
  margin-right: 5px;
}

.newTag {
  margin-top: 20px;
}

.tag-input {
  height: 40px;
}

.enterTag {
  margin-left: 5px;
  margin-right: 5px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

